  }

  .btn-responsive {
    font-style: Medium;
    border-style: none;
    font-size: 14px;
    width: 7.5rem;
    height: 5rem;
    box-shadow: 0px 2px 2px black;
    color: #acccd8;
  }
  .btn-container-responsive {
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
  }
  .btn-search {
    background-color: #acccd8;
    border-radius: 4px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
    width: 18rem;
    border-style: none;
    height: 2.5rem;
  }
}

