.bar-menu {
  display: flex;

  justify-content: space-between;
  align-items: center;
  padding: 16px;
  fill: solid #252e41;
  width: auto;
  height: 7vh;
  left: 0px;
  top: 0px;
  background: #ffffff;
}

.logo {
  width: 0.5rem;
  height: 3.5rem;
}

.btn-container {
  display: flex;
  justify-content: right;
}
.btn-container-responsive {
  display: none;
}

.btn-sesion.btn-sesion {
  align-items: center;
  padding: 8px 22px;
  border-style: none;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
  position: static;

  top: 0%;
  bottom: 0%;

  /* Primario/fondo oceanico */

  background: #252e41;
  border-radius: 4px;

  /* Inside Auto Layout */

  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 10px 10px;

  font-style: Medium;
  font-size: 15px;
  line-height: 26px;
  line-height: 148%;
  color: white;
  font-weight: bold;
}

.btn-sesion:hover {
  cursor: pointer;
}

.btn-ins:hover {
  cursor: pointer;
}

.btn-ins {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px 22px;
  border-style: none;
  position: static;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);

  /* Primario / espuma de mar */

  background: #acccd8;
  border-radius: 4px;

  /* Inside Auto Layout */

  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 10px;

  font-weight: bold;
  font-size: 15px;
  line-height: 26px;
  line-height: 148%;
  color: white;
}
.logs {
  display: flex;
  flex-direction: column;
  justify-content: left;
  background-image: url("../../assets/image-3.png");
  height: 100vh;
  width: auto;
}

.log {
  background-color: white;
  box-shadow: 0px 2px 2px black;
}

.logTitle-Container {
  display: flex;
  justify-content: space-between;
}
.log-title {
  font-style: Regular;
  font-size: 34px;
  line-height: 42px;
  line-height: 124%;
  fill: #252e41;
  color: #252e41;
}

.btn-x {
  background-color: white;
  border: none;
}

.btnLog-container {
  display: flex;
  justify-content: center;
}

.p-sigin {
  color: #252e41;
}

.btn-language {
  display: flex;
  flex-direction: column;
}

.hello-title {
  font-style: Bold;
  margin-right: 2rem;
  font-size: 19px;
  line-height: 22px;
  line-height: 100%;
  fill: #252e41;
  color: #252e41;
}

.div-responsive {
  display: none;
}

.search {
  background-color: #6a8f93;
  height: 22rem;
  width: 26rem;
  padding: 2rem;
  margin-left: 2rem;
}

.btn-search {
  background-color: #acccd8;
  border-radius: 4px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
  width: 24rem;
  border-style: none;
  height: 2.5rem;
}
.inp-search {
  background-color: #dfdfdf;
  color: black;
  height: 3.5rem;
  width: 24rem;
  border-style: none;
}
.select-search {
  background-color: #dfdfdf;
  color: black;
  height: 3.5rem;
  width: 12rem;
}

.section-cards {
  background-color: white;
}
/* responsive */
@media (max-width: 800px) {
  .headerResponsive-container {
    display: flex;
  }
  .btn-container {
    display: none;
  }
  .div-responsive {
    display: flex;
    justify-content: center;
    background-color: #252e41;
  }
  .div-responsiveUser {
    display: flex;
    justify-content: center;
    background-color: #252e41;
    margin-right: 1rem;
  }
  .hello-titlerespon {
    margin-right: 0.4rem;
  }

  .btn-nav-responsive.btn-nav-responsive {
    color: #ffffff;
    margin-right: 0.5rem;
  }

  .btn-responsive {
    font-style: Medium;
    border-style: none;
    font-size: 14px;
    width: 7.5rem;
    height: 5rem;
    box-shadow: 0px 2px 2px black;
    color: #acccd8;
  }
  .btn-container-responsive {
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
  }
  .btn-search {
    background-color: #acccd8;
    border-radius: 4px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
    width: 18rem;
    border-style: none;
    height: 2.5rem;
  }
}
